@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700,800&display=swap);
body {
  font-family: "Open Sans", sans-serif;
}

.app {
  position: relative;
  width: 100vw;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.form {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin-top: 20px;
}

.form label {
  margin-right: 10px;
  font-weight: 400;
}

.form input {
  width: 300px;
  line-height: 40px;
  padding: 0;
}

.form input::-webkit-input-placeholder {
  padding-left: 10px;
}

.form input:-ms-input-placeholder {
  padding-left: 10px;
}

.form input::-ms-input-placeholder {
  padding-left: 10px;
}

.form input::placeholder {
  padding-left: 10px;
}

.form button {
  width: 120px;
  line-height: 42px;
  background-color: #3195c9;
  color: #fff;
  border: none;
  transition: all 0.2s ease-in-out;
}

.form form button:nth-child(4) {
  background-color: inherit;
  color: #000;
  border: 1px solid #a9a9a9;
  line-height: 40px;
}

.form button:hover {
  background-color: #2386c0;
}

.cart {
  position: absolute;
  top: 35px;
  right: 60px;
}

.cart_number {
  position: absolute;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 30px;
  height: 30px;
  background-color: #ff0033;
  color: #fff;
  font-weight: 600;
  border-radius: 15px;
}

.fa-shopping-cart {
  position: absolute;
  top: -15px;
  right: -15px;
  z-index: -1;
  font-size: 40px;
  color: #0277bd;
}

.products {
  -webkit-flex-basis: 60%;
          flex-basis: 60%;
  border-bottom: 1px solid #ddd;
  margin: 0 auto;
  min-height: 30vh;
}

.products:hover {
  background-color: #ddd;
}

.product_item {
  display: -webkit-flex;
  display: flex;
  height: 100%;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

.product_item:hover {
  background-color: #ddd;
}

.image_item {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-basis: 20%;
          flex-basis: 20%;
  padding: 0;
  margin: 20px 0;
}

.image_item:hover {
  cursor: pointer;
}

.content {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  -webkit-flex-basis: 60%;
          flex-basis: 60%;
}

.content p:nth-child(2) {
  margin-bottom: 4rem;
  font-size: 16px;
}

.btn_show_modal {
  border: none;
  background-color: inherit;
  padding: 10px 20px 10px 0;
  font-size: 24px;
  text-align: left;
  transition: all 0.2s ease-in-out;
}

.btn_show_modal:hover {
  color: #2386c0;
}

.btn_show_modal p {
  margin: 0;
}

.cart_form {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  width: 100%;
  margin-bottom: 1rem;
}

.cart_form form {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  width: 130px;
}

.cart_form label {
  margin: 0;
  font-weight: 400;
}

.cart_form input {
  width: 50px;
  height: 30px;
  border: 1px solid #999999;
  text-align: center;
}

.cart_form input[type="number"]::-webkit-inner-spin-button,
.cart_form input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.cart_form button {
  -webkit-flex-basis: 25%;
          flex-basis: 25%;
  height: 30px;
  margin-left: 50px;
  background-color: #3195c9;
  color: #fff;
  border: none;
  transition: all 0.2s ease-in-out;
}

.cart_form button:hover {
  background-color: #2386c0;
}

.overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
}

.overlay.show {
  display: block;
  z-index: 4;
}

.modal {
  display: none;
  background: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 80vh;
  height: 90vh;
}

.modal.show {
  display: block;
}

.btn_hide {
  display: none;
}

.btn_close {
  display: inline-block;
  position: absolute;
  right: 0;
  top: 0;
  padding-right: 5px;
  color: #000;
  text-decoration: none;
  font-size: 24px;
  font-weight: 300;
}

.btn_close:hover {
  cursor: pointer;
}

.modal_content {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  padding: 30px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: flex-start;
          align-items: flex-start;
}

.modal_content p {
  margin: 0;
}

.modal_content p:nth-child(3) {
  font-size: 24px;
}

.modal_content p:nth-child(4) {
  font-size: 20px;
}

.modal_content p:nth-child(5) {
  font-size: 12px;
  margin: 0;
  text-align: justify;
}

.modal_content .cart_form {
  display: -webkit-flex;
  display: flex;
}

.image {
  width: 65%;
  margin: 0 auto;
}

img {
  display: block;
  max-height: 150px;
  max-width: 210px;
}

.img {
  display: block;
  width: 100%;
  margin: 0 auto;
}

.pagination {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin: 50px 0;
}

